.App {
  text-align: center;
}

header {
  background-color: #000;
  padding: 20px 0;
  display: flex;
}

header .logo {
  display: inline-block;
  width: 50%;
  text-align: left;
  padding-left: 20px;
}

header nav {
  display: inline-block;
  width: 50%;
  text-align: right;
  padding-right: 20px;
  align-self: center;
}

header nav a {
  font-size: 16px;
  color: #fff;
  text-decoration: none;
  text-transform: uppercase;
}

.Posts {
  max-width: 1040px;
  margin: 100px auto;
}
.Posts .Post {
  display: inline-block;
  width: 45%;
  margin: 10px;
  border: 1px solid #ddd;
  padding: 10px;
  border-radius: 5px;
}

.Posts .Post img {
  width: 100%; 
  height: auto;
}
